.main-nav {
  position: relative;
  width: 100%;
  z-index: 3;

  .main-nav__toggle {
    top: -50px;
    right: 20px;
  }

  .main-nav__wrapper {
    background: #ffffff;
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
  }

  &--closed {
    .main-nav__toggle {
      background: url("../img/but-open.svg") right center no-repeat;
    }

    .main-nav__wrapper {
      display: none;
    }
  }

  &--nojs {
    .main-nav__toggle {
      display: none;
    }

    .main-nav__wrapper {
      display: block;
      position: relative;
    }
  }

  &--opened {
    .main-nav__toggle {
      background: url("../img/but-close.svg") right center no-repeat;
    }
  }
}

@media (min-width: $tablet-width) {
  .main-nav {
    flex-direction: row;
    width: 440px;

    &__toggle {
      display: none;
    }

    .main-nav__wrapper {
      background: transparent;
      display: block;
      position: relative;
    }
  }
}

@media (min-width: $desktop-width) {
  .main-nav {
    min-width: 478px;
    max-width: 478px;

    .main-nav__wrapper {
      display: block;
      position: relative;
    }
  }
}
